document.addEventListener("DOMContentLoaded", () => {
	startApp();
});

function startApp() {
	fixedNavegation();
	createGallery();
	scrollNav();
}

function fixedNavegation() {
	const body = document.querySelector("body");
	const bar = document.querySelector(".header");
	const aboutFestival = document.querySelector(".container-festival");

	window.addEventListener("scroll", function () {
		if (aboutFestival.getBoundingClientRect().top < 0) {
			bar.classList.add("fixed");
			body.classList.add("body-scroll");
		} else {
			bar.classList.remove("fixed");
			body.classList.remove("body-scroll");
		}
	});
}

function scrollNav() {
	const links = document.querySelectorAll(".principalNavegation a");
	links.forEach(link => {
		link.addEventListener("click", function (e) {
			e.preventDefault();

			const sectionScroll = e.target.attributes.href.value;
			const section = document.querySelector(sectionScroll);
			section.scrollIntoView({ behavior: "smooth" });
		});
	});
}

function createGallery() {
	const gallery = document.querySelector(".gallery-images");

	for (let i = 1; i <= 12; i++) {
		const image = document.createElement("picture");
		image.innerHTML = `
        <source srcset="build/img/thumb/${i}.avif" type="image/avif">
        <source srcset="build/img/thumb/${i}.webp" type="image/webp">
        <img loading="lazy" width="300" height="200" src="build/img/thumb/${i}jpg" alt="Imagen galeria">
        `;
		image.onclick = function () {
			showImage(i);
		};
		gallery.appendChild(image);
	}
}

function showImage(id) {
	const image = document.createElement("picture");
	image.innerHTML = `
    <source srcset="build/img/grande/${id}.avif" type="image/avif">
    <source srcset="build/img/grande/${id}.webp" type="image/webp">
    <img loading="lazy" width="300" height="200" src="build/img/grande/${id}.jpg" alt="Imagen galeria">
    `;

	// Crear ovelay de la imagem
	const overlay = document.createElement("DIV");
	overlay.appendChild(image);
	overlay.classList.add("overlay");
	overlay.onclick = function () {
		const body = document.querySelector("body");
		body.classList.remove("fix-body");
		overlay.remove();
	};

	// Boton para cerrarlo
	const closeModal = document.createElement("P");
	closeModal.textContent = "X";
	closeModal.classList.add("btn-close");
	closeModal.onclick = function () {
		const body = document.querySelector("body");
		body.classList.remove("fix-body");
		overlay.remove();
	};
	overlay.appendChild(closeModal);

	// AÃ±adirlo al HTML
	const body = document.querySelector("body");
	body.appendChild(overlay);
	body.classList.add("fix-body");
}
